	const db = uniCloud.database() //cdb
	
	export default{
		//  
	async getType(){
		let data=''
			await  db.collection('goods_type').get()
			  .then((res)=>{
			    // res 
				console.log(res)
				data= res.result
			  }).catch((err)=>{
			    console.log(err.code); // 
			        console.log(err.message); // 
			  })
			  
			  return data
		}
	}
